.App {
  text-align: center;
  height:100vh;
  width: 100vw;
  display:flex;
  flex-flow:row;
  flex-wrap: wrap;
}
.sideBar{
  width: 35%;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.sideBar{
  width: 25%;
}
#search{
  width: 100%;
  height:2rem;
  font-size:1.2rem;
  padding:6%
}
.venueList{
  padding:1rem;
}
.venueList .listItem{
  list-style: none;
  font-size:1rem;
  box-shadow: 1px 1px 1px rgba(0,0,0,0.3);
  padding:0.5rem;
  margin-top:1rem;
  cursor:pointer;
  background: red;
  color: white;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
